body html {
  margin: 0;
  min-height: 100%;
}

body {
  background-color: #e0e0e0;
}

.page {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 80%;
  margin: auto;
  background-color: white;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: #01579b;
  width: 100%;
  height: 125pt;
}

.title {
  color: white;
}

.navigation {
  display: flex;
  flex-direction: row;
}

.navigation-el {
  margin: 10pt;
  color: white;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: white;
  cursor: pointer;
  margin: 10pt;
  transition: all 0.5s ease;
}

a.selected {
  color: #81d4fa;
}

.content {
  width: 100%;
}

.section {
  margin: 0 15pt;
  padding: 15pt;
  color: #01579b;
}

.vision {
  margin: 15pt;
  text-align: center;
  font-size: 1.5em;
  background-color: white;
  color: #01579b;
}

.home {
  background-color: white;
}

.about {
  background-color: white;
}

.about-image {
  text-align: center;
}

.contact {
  background-color: white;
}

.left-right {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.left-right .text {
  padding: 15pt;
}

.left-right * {
  width: 50%;
  height: 50%;
  font-size: 1.25em;
}

.about-img {
  width: 100%;
  Height: 100%;
}

.copywrite {
  margin: 20pt;
  text-align: center;
}

.sticky {
  position: fixed;
  top: 0;
  width: 80%;
}
